.main-nav{
    min-height: 50px;
    width: 100%;
    margin: 0% auto;
    border-top: solid 3px rgb(59, 25, 197);
    box-shadow: 0px 1px 5px black;
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    background-color: whitesmoke;
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar{
    height: 100%;
    min-width: 85%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.nav-logo{
    padding: 0px;
    margin: 0px;
    text-decoration: none;
    align-items: center;
    justify-content: center;
    color: black;
    font-weight: 500;
    display: flex;
}
.nav-logo1:hover{
    background-image: url('../../images/blogo.png');
}
.nav-item{
    margin: 0px 3px;
    font-size: medium;
    font-weight: 500;
    text-decoration: none;
    color: black;
    transition: 0.2s;
}
.nav-btn{
    cursor: pointer;
    border-radius: 20px;
    padding: 10px 20px;
}
.nav-item:hover{
    background-color: white;
    color: blue;
    transition: 0.5s;
}
.navbar form{
    flex-grow: 1;
    padding: 0px 12px;
    position: relative;
}
.navbar form input{
    min-width: 90%;
    margin: 0;
    padding: 8px 10px 8px 32px;
    font-size: 13px;
    border: solid 1px black;
    border-radius: 10px;

}
.navbar form input:hover{
    border: solid 1px rgb(54, 41, 169);
}
.navbar form .search-icon{
    position: absolute;
    left: 25px;
    top: 8px;

}
.nav-links{
    padding: 7px 13px;
    border: solid 1px rgb(46, 46, 48);
    border-radius: 10px;
    background-color: #fbf3d5;
    cursor: pointer;
}
.nav-links:hover{
    background-color: white;
    color: blue;
    transition: 0.5s;
    border: solid 1px rgb(64, 85, 222);
}