.user-List-container{
    margin: 0px;
    padding: 10px 0px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px,1fr));
    gap: 10px;
}
.hom2{
    margin-top: 100px ;
    margin-left: 30px;
    width: 80%;
}
.user-profile-link{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    text-decoration: none;
    color: black;
}
.user-profile-link h3{
    padding: 10px 13px;
    background-color: blue;
    border-radius: 50%;
}
.user-profile-link h5{
    margin: 0px 10px;
}
