.question-details-page{
    width: 100%;
    float: left;
    padding: 10px 20px;
    margin: 90px 0px 20px 20px;
    background-color: azure;
    box-shadow: 0 10px 25px rgb(0 0 0/ 5%),
    0 20px 48px rgb(0 0 0/ 5%),
    0 1px 4px rgb(0 0 0/ 5%);
}
.question-deatils-container{    
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: solid 1px black;
}
.question-deatils-container-2{
    display: flex;
}
.question-votes{
    padding: 5px 20px 5px 10px;
}
.question-votes p{
    margin: 0%;
    font-size: 25px;
    text-align: center;
}
.votes-icon{
    font-size: 40px;
    cursor: pointer;
    color: aqua;
}
.votes-icon:active{
    color: blue;
}
.question-details-container .question-body{
    line-height: 22px;
    white-space: pre-line; 
}
         
.question-deatils-container .question-deatils-tags{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.question-deatils-container .question-deatils-tags p,
.post-ans-container p .ans-tag{
    padding: 5px 5px;
    margin: 3px;
    font-size: 13px;
    border-radius: 2px;
    background-color:  #e1ecf4;
    color:#39739d;
    text-decoration: none;
    line-height: 22px;

}
.question-action-user{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.question-action-user button, .edit-question-btn{
    border: none;
    padding: 10px 5px;
    margin: 0px 10px 0px 0px;
    text-decoration: none;
    color: whitesmoke;
    cursor: pointer;
    font-size: 14px;
    transition: 0.5s;
    border-radius: 15px;
    width: 70px;
}
.btn-share{
    background-color:#198754;
}
.btn-share:hover{
    background-color:#157347;
}
.btn-delete{
    background-color: #dc3545;
}
.btn-delete:hover{
    background-color:#bb2d3b;
}

.question-actions-user button:active{
    border-bottom: solid 2px black;
}
.question-actions-user div:nth-child(2) p,
.question-actions-user div:nth-child(2) .user-link{
    text-decoration: none;
    font-size: 14px;
    margin: 0%;
}
.user-link{
    display: flex;
    align-items: center;
}
.user-link div{
    padding-left: 10px;
}
.post-ans-container form textarea{
    padding: 10px;
    border: solid 1px black;
    font-family: 'Robot',sans-serif;
    width: calc(100%-20px);
    resize: vertical;
}
.post-ans-container form .post-ans-btn{
    margin: 20px 0px;
    padding: 10px 10px;
    background-color:rgb(64, 85, 222);
    color:whitesmoke;
    border: solid 1px blue;
    border-radius: 15px;
    cursor: pointer;
    transition: 0.5s all;
}
.post-ans-container form .post-ans-btn:hover{
    background-color: rgb(101, 115, 205);
}
.diplay-ans{
    padding-bottom: 20px;
    border-bottom: solid 1px black;
}
.diplay-ans p{
    font-size: 14px;
    line-height: 18px;
    white-space: pre-line;
}