.home-container-3{
    margin: 20px;
}
.tags-h1{
    margin-top: 50px;
    font-weight: 400;
}
.tags-p{
    margin: 0;
    font-size: 15px;
}
.tags-list-container{
    padding: 30px 0px;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(200px , 1fr));
    gap: 10px;
}
.tag{
    padding: 5px 15px;
    border: solid 1px black;
    border-radius: 10px 30px ;
    background-color:  rgb(220, 239, 253);
    box-shadow: 0 10px 25px rgb(0 0 0/5%),
    0 20px 48px rgb(0 0 0/5%),
    0 1px 4px rgb(0 0 0/5%);
}
.tag h5{
    display: inline-block;
    margin: 10px 0px;
    padding: 5px 15px;
    background-color: rgb(64, 85, 222);
    color: antiquewhite;
    border-radius: 10px 30px ;
}
.tag p{
    font-size: 14px;
    color: black;
    line-height: 17px;
}